export default {
	name: 'UniGrid',
	props: {
		options: {
			type: Array,
			default () {
				return []
			}
		},
		act:{
			type: String,
			default: ''
		},
		needAct:{
			type: Boolean,
			default: false
		},
		type: { // 布局格式，长方形oblong，正方形square
			type: String,
			default: 'square'
		},
		size:{ //图片大小
			type:String
		},
		columnNum: { // 每一行有多少个
			type: [Number, String],
			default: 3
		},
		showOutBorder: { // 显示外边框
			type: Boolean,
			default: true
		},
		showBorder: { // 是否显示border，如果为false，showOutBorder无效
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			actId:''
		}
	},
	computed: {
		gridGroup() {
			let group = []
			let groupItem = []
			this.options && this.options.forEach((item, index) => {
				groupItem.push(item)
				if (index % this.columnNum === this.columnNum - 1) {
					group.push(groupItem)
					groupItem = []
				}
			})
			if (groupItem.length > 0) {
				if (this.columnNum > groupItem.length) {
					for (let i = 0, length = groupItem.length; i < this.columnNum - length; i++) {
						groupItem.push({
							seize: true
						})
					}
				}
				group.push(groupItem)
			}
			groupItem = null
			return group
		}
	},
	created() {
		this.columnNumber = this.gridGroup[0].length
	},
	onShow(){
		if(this.act){
			this.actId=this.act;
		}else{
			this.actId=''
		}
	},
	onLoad(){
		if(this.act){
			this.actId=this.act;
		}else{
			this.actId=''
		}
	},
	methods: {
		onClick(index, num,item) {
			console.log('clickItem:',item)
			this.$emit('click', {
				index: index * this.columnNumber + num
			})
			/* if(this.needAct){
				this.actId = item.categoryId
			} */
			if(item.url){
				let url = ''
				if(item.categoryId){
					url = item.url + '&categoryId='+ item.categoryId
				}else{
					url = item.url
				}
				uni.navigateTo({
					url: url
				});
			}
		}
	}
}
